* {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: 0;
  font-family: "Roboto", -apple-system, system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: 0.15s ease-in-out;
}

.todo {
  display: flex;

  position: absolute;
  left: 50%;
  top: 50%;
  width: calc(100vw - 30px);
  height: calc(100vh - 100px);
  min-width: 750px;
  transform: translate(-50%, -50%);
  box-shadow: 1px 2px 20px #d1d1d1;
  border: 1px solid #f1f1f1;
  font-size: 14px;
  &__tasks {
    flex: 1;
    padding: 60px;
    overflow: auto;
  }

  &__sidebar {
    background-color: #f4f6f8;
    width: 200px;
    //height: calc(100% - 120px);
    border-right: 1px solid #f1f1f1;
    padding: 60px 20px;
  }

  .button {
    background: #4dd599;
    border-radius: 4px;
    color: #fff;
    border: 0;
    padding: 10px 20px;

    cursor: pointer;

    &:hover {
      background: darken(#4dd599, 7%);
    }

    &:active {
      background: darken(#4dd599, 11%);
    }
    &--grey {
      margin-top: 15px;
      margin-left: 10px;
      background: rgb(228, 228, 228);
      color: rgb(94, 94, 94);
      &:hover {
        background: darken(rgb(228, 228, 228), 7%);
      }

      &:active {
        background: darken(rgb(228, 228, 228), 11%);
      }
    }
  }

  .field {
    width: 100%;
    background: #ffffff;
    border: 1px solid #efefef;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 8px 12px;

    &--task {
      margin-top: 15px;
      margin-bottom: -15px;
    }
    &:focus {
      border-color: #dfdada;
    }
  }
}
